*{
    margin: 0px;
    padding: 0px;
}

html{
    scroll-behavior: smooth;
}

#Navbar{
    font-family: 'Fresca', sans-serif;
    display: flex;
    align-items: center;
    position: relative;
    top: 0px;
    @media (max-width: 1170px){
        flex-direction: column;
    }
}

#Navbar::before{
    content: "";
    background-color: black;
    position: absolute;
    top: 0px;
    left: 0px;
    height: 100%;
    width: 100%;
    z-index: -1;
    opacity: 1.5;
    transition: all 0.5s ease-in-out;
}

#logo{
    margin: -60px 20px
    
}

#logo img{
    height: 200px;
    margin: 15px 30px;
    transition: all 0.5s ease-in-out;
}

#Navbar ul{
    display: flex;
    @media (max-width: 1170px){
        margin-bottom: 15px;
    }
}

#Navbar ul li{
    list-style: none;
    font-size: 1.5rem;
    @media (max-width: 1170px) {
        font-size: 1.0rem;        
    }
}

#Navbar ul li a{
    display: block;
    text-decoration: none;
    padding: 3px 22px;
    padding-top: 15px;
    border-radius: 20px;
    color: white;
    @media (max-width: 1170px) {
        padding: 0px 8px;        
    }
    transition: all 0.3s ease-in-out;
}

#Navbar ul li a:hover{
    color: rgb(36, 36, 36);
}

#Navbar:hover img{
    filter: invert(100%);
}

#Navbar:hover a{
    color: black;
    
}

#Navbar:hover::before{
    content: "";
    background-color: rgb(184, 182, 182);
    position: absolute;
    top: 0px;
    left: 0px;
    height: 100%;
    width: 100%;
    z-index: -1;
    opacity: 0.9;
}