@media(max-width: 1170px){
    #boxes{
        flex-direction: column;
        overflow: hidden;
    }
}

#services{
    font-family: 'Fresca', sans-serif;
}

.h-primary{
    font-size: 2.0rem;
    padding: 15px;
    font-weight: bold;
    text-align: center;
    padding-top: 30px;
    @media (max-width: 1170px) {
        padding: 0px;
        padding-top: 34px;        
    }
}

.h2-primary{
    font-size: 1.4rem;
    text-align: center;
    cursor: pointer;
}

#boxes{
    margin: 34px;
    display: flex;
    justify-content: center;
    align-items: center;
}

#boxes .box{
    border: 2px solid red;
    padding: 10px 34px;
    margin: 5px 15px;
    border-radius: 17px;
    background-color: rgb(235 235 235);
    @media (max-width: 1170px) {
        padding: 10px 20px;        
    }
    transition: all 0.2s ease-in-out;
}

#boxes .box:hover{
    transform: scale(1.04);
    background-color: rgb(200, 215, 230);
}

#boxes .box img{
    display: block;
    height: 250px;
    margin: auto;
    cursor: pointer;
    @media (max-width: 1170px) {
        height: 120px;       
    }
    transition: all 0.2s ease-in-out;
}

.box:hover img{
    transform: scale(0.91);
} 

#boxes .box p{
    text-align: center;
    cursor: pointer;
}