#home{
    font-family: 'Fresca', sans-serif;
    display: flex;
    flex-direction: column;
    padding: 10px 200px;
    height: 440px;
    justify-content: center;
    align-items: center;

    @media(max-width: 1170px){
        height: 390px;
        padding: 10px 70px;
    }
}

#home::before{
    content: "";
    background: url('Toys-bg2.jpg') no-repeat center center/cover;
    position: absolute;
    top: 0px;
    left: 0px;
    height: 570px;
    width: 100%;
    z-index: -1;
    opacity: 0.7;
    transition: 8s;

    animation-name: bg-image;
    animation-direction: alternate-reverse;
    animation-play-state: running;
    animation-timing-function: ease-in;
    animation-duration: 15s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
}
@keyframes bg-image{
    0%{
        background: url('Watch-bg.jpg') no-repeat center center/cover;
    }
    50%{
        background: url('Gift-bg2.jpg') no-repeat center center/cover;
    }
}

#home h1{
    font-size: 2.8rem;
    @media (max-width: 1170px) {
        font-size: 2.1rem;        
    }
    padding: 15px;
    text-align: center;
    color: black;
}

#home p{
    text-align: center;
    font-size: 1.2rem;
    @media (max-width: 1170px) {
        font-size: 0.9rem;        
    }
    color: black;
}


