
#Contact{
    font-family: 'Fresca', sans-serif;
    position: relative;
}

#Contact::before{
    content: "";
    background: url('contactbg.jpg') no-repeat center center/cover;
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: -1;
    opacity: 0.5;
}

#Contact h1{
    text-align: center;
    padding: 15px;
    font-size: 2.0rem;
    font-weight: bold;
}

#form{
    display:flex;
    justify-content: center ;
    align-items: center;
    padding-bottom: 34px;
}

#form form{
    width: 40%;
}

#form .formgroup{
    padding: 10px;
}

#form label{
    font-size: 1.5rem;
}

#form input{
    padding: 15px;
    border-radius: 5px;
    width: 100%;
    transition: all 0.2s ease-in-out;
}

#form input:hover{
    transform: scale(1.02);
}

#form textarea{
    width: 100%;
    font-size: 1.0rem;
    border-radius: 5px;
    padding: 15px;
    @media (max-width: 425px) {
        width: 130px;
        height: 60px;       
    }
    transition: all 0.2s ease-in-out;
}

#form textarea:hover{
    transform: scale(1.02);
}