#Clientsection{
    position: relative;
    font-family: 'Fresca', sans-serif;
}

#Clientsection::before{
    content: "";
    background: url('toy-bg4.jpg') no-repeat center center/cover;
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: -1;
    opacity: 0.5;
}

#Clientsection h1{
    font-size: 2.0rem;
    font-weight: bold;
    padding: 15px;
    text-align: center;
}

#Clients{
    display: flex;
    justify-content: center;
    align-items: center;
    @media(max-width: 1170px){
        flex-wrap: wrap;
    }
}

#Clients .Client-items{
    padding: 10px 34px;
    margin: 5px 8px;
    @media(max-width: 1170px){
        padding: 0px 5px;
    }
}

#Clients .Client-items img{
    cursor: pointer;
    height: 150px;
    @media(max-width: 1170px){
        height: 85px;
    }
    transition: all 0.2s ease-in-out;
}

#Clients .Client-items img:hover{
    transform: scale(0.9);
}